
import './App.css';
import {BrowserRouter as Router,Route, Switch} from "react-router-dom";
import JobPostings from "./components/JobPostings";
import Candidates from "./components/Candidates";

function App() {
  return (
      <Router>
        <Switch>
          <Route exact path="/jobposting/:id/candidate">
            <Candidates/>
          </Route>
          <Route path="/">
            <JobPostings />
          </Route>
        </Switch>
      </Router>
  );
}

export default App;
